body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.hand {
    position: absolute;
    bottom: 450px;
    left: 400px;
    -webkit-transform-origin: 50px 0px;
            transform-origin: 50px 0px;
  }
  
  .hand .arm {
    position: absolute;
    bottom: 0px;
    left: 45px;
    margin: 0;
    padding: 0;
    height: 380px;
    width: 10px;
    background-color: black;
    z-index: 100;
  }
  
  .hand img {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100px;
    z-index: 200;
  }
  
.element {
    position: absolute;
    bottom: 450px;
    left: 375px;
    height: 420px;
    -webkit-transform-origin: 75px 420px;
            transform-origin: 75px 420px;
  }
  
.wheel-input {
    display: block;
    text-align: right;
}

.wheel-input img {
    height: 50px;
    margin-right: 30px;
}

.wheel-input input {
    width: 400px;
}

.app {
  position: relative;
  display: -webkit-flex;
  display: flex;
  z-index: 0;
}

